.design-summary {
    /* padding-top:50px; */
    color:#000;
    align-content: center; 
    width: 90%;
    margin:auto;
    min-height:calc(100vh - 730px);
  }
  
  .general-header {
    font-size: 33px;
    padding-bottom:8px;
    width:300px;
    margin:auto;
    border-bottom: 2px solid #000;
  }

.general-paragraph {
    padding-top:40px;
    padding-bottom:30px;
    width: 70%;
    max-width: 900px;
    margin:auto;
    font-size: 20px;
    line-height: 1.7em; 
    text-align: justify;
  }

  @media only screen and (max-width: 800px) {
    .design-summary {
      padding-top:20px;
      color:#000;
      align-content: center; 
      width:90%;
      max-width:600px;
      margin:auto;
    }
    
    .general-header {
      font-size: 18px;
      padding-bottom:8px;
      width:150px;
      margin:auto;
      border-bottom: 1px solid rgb(129, 129, 129);
    }
  
  .general-paragraph {
      padding-top:20px;
      padding-bottom:30px;
      width: 100%;
      margin:auto;
      font-size: 10px;
      line-height: 1.5em; 
    }

  }